body {
  background-color: #bcd0f8;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cashoutbutton {
  margin-left: 1750px;
  top: 30px;
  background-color: #515766;
  border: none;
  color: white;
  font-size: 17px;
  position: absolute;
}

.cashoutbutton:hover {
  margin-left: 1750px;
  background-color: #8f94a1;
  border: none;
  color: white;
  font-size: 17px;
}

.addtocart {
  background-color: #515766;
  border: none;
  color: white;
  font-size: 17px;
  padding: 10px 40px;
  border-radius: 7px;
}

.addtocart:hover {
  background-color: #8f94a1;
  border: none;
  color: white;
  font-size: 17px;
  padding: 10px 40px;
  border-radius: 7px;
}

.cart {
  align-items: center;
  justify-content: center;
  padding: 10px;
  border: #282c34 2px solid;
  border-radius: 10px;
  width: 300px;
  height: 500px;
  background-color: #515766;
  color: white;
  position: absolute;
  z-index: 10;
  left: 1550px;
  top: -170px;
}

.subtotal {
  align-items: center;
  justify-content: center;
  width: 300px;
  z-index: 11;
  left: 1560px;
  top: 130px;
  position: absolute;
  color: white;
  font-size: large;
  border: white 2px solid;
}

.mpbuttons {
  z-index: 11;
  color: rgb(255, 255, 255);
  font-size: larger;
  border: 0px solid;
  background-color: #515766;
}

.emptycart {
  z-index: 11;
  color: #515766;
  font-size: medium;
  margin-top: 10px;
  border: 1px solid white;
  background-color: #3a3f49 solid;
  border-radius: 7px;
}

.emptycart:hover {
  background-color: #778096;
  border: #515766 1px solid;
  color: white;
}

.checkoutbtn {
  align-items: center;
  justify-content: center;
  width: 150px;
  z-index: 11;
  left: 75px;
  top: 180px;
  position: absolute;
  border-radius: 7px;
  border: 1px solid white;
  background-color: #3a3f49 solid;
  font-size: medium;
}

.checkoutbtn:hover {
  background-color: #778096;
  border: #515766 1px solid;
  color: white;
}

.confirmbtn {
  align-items: center;
  justify-content: center;
  width: 150px;
  z-index: 11;
  left: 255px;
  top: 920px;
  position: absolute;
  border-radius: 7px;
  border: 1px solid white;
  background-color: #3a3f49 solid;
  font-size: medium;
}

.confirmbtn:hover {
  background-color: #778096;
  border: #515766 1px solid;
  color: white;
}

.checkoutbox {
  align-items: center;
  justify-content: center;
  width: 1100px;
  height: 550px;
  z-index: 12;
  left: 400px;
  top: -1000px;
  position: absolute;
  color: white;
  font-size: large;
  border: white 2px solid;
  background-color: #515766;
}

.summarybox {
  align-items: center;
  justify-content: center;
  width: 600px;
  padding: 30px;
  height: 900px;
  z-index: 12;
  left: 33%;
  top: -1100px;
  position: absolute;
  color: white;
  font-size: large;
  border: white 2px solid;
  background-color: rgba(81, 87, 102, 1);
}

.summaryproducts {
  z-index: 14;
  left: 40%;
  top: 57%;
  position: absolute;
  color: white;
}

.total {
  color: rgb(250, 58, 58);
}

.sumtext {
  color: rgb(223, 185, 20);
}

.summarycontainer {
  position: relative;
}

.cartcontainer {
  position: relative;
}

.images {
  height: 300px;
  width: 300px;
  max-width: 100%;
  border-radius: 10px;
}

.storeobj {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
  padding: 10px;
  border: #282c34 2px solid;
  border-radius: 10px;
  width: 300px;
  height: 500px;
  background-color: #282c34;
  color: white;
}

.storelist {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: 400px;
  margin-right: 400px;
}

table {
  border-collapse: collapse;
  width: 100%;
  border: 1px solid;
}

th {
  text-align: center;
  padding: 8px;
  border: 1px solid;
}
